import VSlider from './components/Slider'
import VSearch from './components/Search'
import SelectView from './components/SelectView'
import { mapState } from 'vuex'

export default {
  components: {
    VSlider,
    VSearch,
    SelectView
  },
  data () {
    return {
      isShowModal: false
    };
  },
  computed: {
  },
  methods: {
    // 展示用户侧边栏
    handleModalShow () {
      this.isShowModal = !this.isShowModal
    },
    changeTab (index) {
      this.TabCur = index
    },
    // 页面跳转
    toDetailPage (url) {
      uni.navigateTo({ url })
    }
  },
  onLoad () {
    if (this.hasLogin) {
      uni.showModal({
        title: '未登录',
        content: '您未登录，需要登录后才能继续',
        /**
         * 如果需要强制登录，不显示取消按钮
         */
        showCancel: !this.forcedLogin,
        success: (res) => {
          if (res.confirm) {
            /**
             * 如果需要强制登录，使用reLaunch方式
             */
            if (this.forcedLogin) {
              uni.reLaunch({
                url: '../login/login'
              });
            } else {
              uni.navigateTo({
                url: '../login/login'
              });
            }
          }
        }
      });
    }
  },
  onShareAppMessage: function (res) {
    if (res.from === 'button') {
      // 来自页面内转发按钮
      console.log(res.target)
    }
    return {
      title: '专注评测市场上最好的产品',
      path: '/pages/main/index'
    }
  }
}
